import React from 'react';
import Youtube from 'react-youtube';

export class MyVid extends React.Component{
    constructor(props){
        super(props);

    }

    _onReady(event){
        event.target.pauseVideo();
    }

    render(){
        return (
            <Youtube videoId={this.props.vid} opts={this.props.opts} onReady={this._onReady} />
        );
    }


}